.main-navigation {
	display: block;
	font-weight: 400;
	//clear: left;
	line-height: 1.2;

	ul { //first level items
		display: none;
		list-style: none;
		margin: 0;
		padding-top: 1em;
		padding-left: 0;

		ul { //second level - submenu
			//display: none;
			top: 1.5em;
			z-index: 99999;

			ul {
				top: 0;
			}

			li {
				padding-left: 1em;

				&:hover > ul,
				&.focus > ul {
					left: 100%;
				}
			}

			a {
				width: 200px;
			}
		}

		li:hover > ul,
		li.focus > ul {
			left: auto;
		}
	}

	li {
		position: relative;
	}

	a {
		display: inline-block;
		width: 100%;
		padding: .5em 1em .5em 0;
		text-decoration: none;
		color: #fff;
	}

	a:hover,
	a:focus {
		text-decoration: underline;
	}

	.current_page_item > a,
	.current-menu-item > a,
	.current_page_ancestor > a,
	.current-menu-ancestor > a {
	}

	.menu-item-has-children,
	.page_item_has_children {
		min-width: 218px;
	}
	.menu-item-has-children > a,
	.page_item_has_children > a {
		padding-right: 2em;
	}
}

button.dropdown-toggle {
	position: absolute;
	right: 0;
	border: none;
	background: inherit;
	color: white;
	line-height: 1em;
	padding: .4em .75em;
}

.menu-toggle {
	position: absolute;
	top: 0;
	right: 0;
	display: block;
	margin: 1.2em 1.2em 0 0;
	padding: .6em .8em;
	font-size: 80%;
	text-transform: uppercase;
	color: white;
	border: 1px solid hsla(0, 0%, 100%, .3);
}

/* Toggle small menu and children on */
.toggled-on ul,
.sub-menu.toggled-on {
	display: block;
}

@media screen and (min-width: $query__small) {
	.menu-toggle {
		display: none;
	}

	.main-navigation {
		position: relative;

		.menu-item-has-children > a,
		.page_item_has_children > a {
			//padding-right: 2em;
		}

		ul { //first level menu items
			display: block;
			width: 100%;
			position: inherit;

			li {
				display: inline-block;
				a {
					padding: .4em 1em;
				}
			}

			ul { //submenu en desktop
				position: absolute;
				left: 0;
				top: 0;


				li {
					padding-left: 0;

					a {
						width: 218px;
						background: none;
					}
				}
			}


		}

	}

}

